import React, { useEffect, useState } from "react";
import Footer from "../../components/footer";
import Navbar from "../../components/navbar";
import Parser from 'html-react-parser';
import AOS from "aos";
import "./style.css";
import gsap from "gsap";
import ScrollTrigger from "gsap/ScrollTrigger";
import ContactForm from "../../common/contactform";
import TeamMember from "../../common/team-member";

import { ENDPOINTS } from "../../app/config/endpoints/api";
import { STATUS_CODE } from "../../app/constants";
import { getRequest } from "../../app/httpClient/axiosClient";

import { Link } from "react-router-dom";
import { ROUTES_PATH } from "../../app/constants";

import Analogy from "./Content/Analogy";
import { useLocation } from "react-router-dom";

function Whybbo() {
  const { pathname } = useLocation();
  useEffect(() => {
    window.scrollTo(0, 0);
  }, [pathname]);

  const [analogy, setAnalogy] = useState({ title: null, description: null });
  useEffect(() => {
    gsap.registerPlugin(ScrollTrigger);

    let tl = gsap.timeline({ delay: 0.5 });
    // tl.from(".img-animate", {
    //   duration: 0.5,
    //   opacity: 0,
    //   //height: 0,
    //   ease: "power2.out",
    // }).to(".img-animate", {
    //   duration: 0.5,
    //   opacity: 1,
    //   //transformOrigin: "top",
    //  // height: 520,
    //   ease: "power2.out",
    // });
    tl.from(".img-animate", {
      duration: 0.5,
      opacity: 0,
      x: -10,
      ease: "Power1.easeOut",
    }).to(".img-animate", {
      duration: 0.5,
      opacity: 1,
      x: 10,
      ease: "Power1.easeOut",
    });
    tl.from(".img-animate-circle", {
      duration: 0.5,
      opacity: 0,
      x: -5,
      ease: "Power1.easeOut",
    }).to(".img-animate-circle", {
      duration: 0.5,
      opacity: 1,
      x: 5,
      ease: "Power1.easeOut",
    });
    tl.from(".img-grid img", {
      duration: 0.5,
      opacity: 0,
      y: -7,
      ease: "Power1.easeOut",
    }).to(".img-grid img", {
      duration: 0.5,
      opacity: 1,
      y: 7,
      ease: "Power1.easeOut",
    });
    // new PureCounter();
  }, []);

  useEffect(() => {
    AOS.init({
      duration: 800,
    });
  }, []);

  const pageContent = async () => {
    const endpoint = [ENDPOINTS.CONENT.WHYBBO];
    var response = await getPageContent(endpoint);

    if (response.status === STATUS_CODE.RES200) {
      var _DATA = response.data.data;

      setAnalogy({
        title: _DATA.description.why_bbo_Analogy_title,
        description: _DATA.description.why_bbo_Analogy_desc,
      });
    }
  };

  async function getPageContent(url) {
    return new Promise((resolve, reject) => {
      getRequest(url)
        .then((res) => {
          resolve(res);
        })
        .catch((error) => {
          reject(error);
        });
    });
  }

  useEffect(() => {
    pageContent();
  }, []);

  return (
    <>
      <Navbar />
      <div className="scroll-wrapper">
        <section className="hero body-padding why-bbo-hero">
          <div className="container">
            <div className="row align-items-center">
              <div
                className="col-md-6 col-sm-6 text-center text-sm-start order-2 order-sm-1 mt-4 mt-sm-0"
                data-aos="fade-up"
              >
                <h1 className="animate-heading">
                  Why<span className="d-block"> BOUNCE BACK</span>
                  <span className="d-block h2 secondary-clr mb-0 mt-2 font-secondary">
                    ORGANIZATION (BBO)
                  </span>
                </h1>
                <Link className="btn btn-primary-outline"  to={ROUTES_PATH.join}>
                  Get Started
                </Link>
              </div>
              <div className="col-md-6 col-sm-6 order-1 order-sm-2 position-relative">
                <div
                  className="img-animate"
                  style={{ background: `url(images/headshot.jpg)` }}
                ></div>
                <div className="img-grid">
                  <img src="images/logo-icon.svg" className="mb-4" alt="logo" />
                  <div
                    className="img-animate-circle"
                    style={{ background: `url(images/black-woman.jpg)` }}
                  ></div>
                </div>
              </div>
              <div className="col-md-12 order-3 text-center">
                <div className="mouse-animate mt-5">
                  <a href="#why-bbo">
                    <img src="images/arrows-down.svg" alt="mouse-wheel" />
                  </a>
                </div>
              </div>
            </div>
          </div>
        </section>

        <section id="why-bbo" className="section-padding" data-aos="fade-up">
          <div className="container">
            <Analogy analogy={analogy} />
            <div className="row text-center">
              <div className="col-sm-4">
                <div className="record-block" data-aos="fade-up">
                  <h3
                    className="counter-number counter-text purecounter"
                    data-purecounter-start="0"
                    data-purecounter-end="5"
                    data-purecounter-duration="1"
                  >
                    5
                  </h3>
                  <h5>Year of Service</h5>
                </div>
              </div>
              <div className="col-sm-4">
                <div className="record-block" data-aos="fade-up">
                  <h3
                    className="counter-number counter-text purecounter certification"
                    data-purecounter-start="0"
                    data-purecounter-end="5"
                    data-purecounter-duration="1"
                    data-title="k"
                  >
                    5
                  </h3>
                  <h5>Provided Certification</h5>
                </div>
              </div>
              <div className="col-sm-4">
                <div className="record-block" data-aos="fade-up">
                  <h3
                    className="counter-number counter-text purecounter"
                    data-purecounter-start="0"
                    data-purecounter-end="30"
                    data-purecounter-duration="1"
                  >
                    30
                  </h3>
                  <h5>Live Session</h5>
                </div>
              </div>
            </div>
            <div className="row text-center mt-5">
              <div className="col-md-12">
                <Link className="btn btn-primary-outline" to={ROUTES_PATH.what_we_do}>
                    Know More
                </Link>
              </div>
            </div>
          </div>
        </section>
        <TeamMember />
        <ContactForm />

        <Footer />
      </div>
    </>
  );
}

export default Whybbo;
