import React, { useState } from "react";
import TextField from "../../../components/text-field";
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import * as yup from "yup";
import { educationalInfoUpdate } from "../../../redux/actions/actions";
import { useDispatch } from "react-redux";
import axios from "axios";
import { socialAccount } from "../../../utils/end-points";
import { useNavigate } from "react-router-dom";

function SocialNetworks({ handleBackSocial }) {
  const dispatch = useDispatch();
  const [width, setWidth] = useState(80);
  const [loading, setLoading] = useState();
  const [fields, setFields] = useState({
    facebook_profile: "",
    instagram_profile: "",
    twitter_profile: "",
    youtube_profile: "",
  });
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFields({ ...fields, [name]: value });
    if (fields.facebook_profile) {
      setWidth(80 + 5);
    }
    if (fields.instagram_profile) {
      setWidth(85 + 5);
    }
    if (fields.twitter_profile) {
      setWidth(90 + 5);
    }
    if (fields.youtube_profile) {
      setWidth(95 + 5);
    }
  };

  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm({
    mode: "onSubmit",
    resolver: yupResolver(schema),
  });

  const navigate = useNavigate();

  const formSubmit = async (data) => {
    try {
      await axios
        .post(socialAccount, data)
        .then((res) => {
          navigate("/coming-soon");
        })
        .catch((err) => console.log(err));
    } catch (error) {
      console.log(error);
    }
  };

  const user = JSON.parse(localStorage.getItem("user"));

  const styles = {
    role: {
      backgroundColor: user?.roleId === "mentor" ? "#1790FF" : "#fdd417",
      width: `100%`,
      color: user?.roleId === "mentor" ? "#fff" : "#000",
    },
  };

  return (
    <form onSubmit={handleSubmit(formSubmit)}>
      <h3>Social Networks</h3>
      <p className="p-default">
        Lorem Ipsum is simply dummy text of the printing and typesetting
        industry. Lorem Ipsum has been the industry's standard dummy text.
      </p>
      <div className="progress">
        <div
          className="progress-bar-profile"
          role="progressbar"
          aria-label="Example with label"
          style={styles.role}
          aria-valuenow="100"
          aria-valuemin="0"
          aria-valuemax="100"
        >
          100%
        </div>
      </div>
      <div className="block-get-touch step-form mt-4">
        <div className="row g-3">
          <div className="col-lg-6 mb-3">
            <TextField
              inputDivStyle={"form-control"}
              type="text"
              name="facebook_profile"
              register={register}
              // value={fields.facebook_profile}
              // handleChange={handleInputChange}
              placeholder="Facebook Profile Link"
              errorMessage={errors?.facebook_profile?.message}
            />
            {/* <input
              type="text"
              className="form-control"
              placeholder="Facebook Profile Link"
              {...register("facebook_profile")}
              name="facebook_profile"
              // value={fields.facebook_profile}
              // onChange={handleInputChange}
            /> */}
          </div>
          <div className="col-lg-6 mb-3">
            <TextField
              inputDivStyle={"form-control"}
              type="text"
              name="twitter_profile"
              register={register}
              // value={fields.twitter_profile}
              // handleChange={handleInputChange}
              placeholder="Twitter Profile Link"
              errorMessage={errors?.twitter_profile?.message}
            />
            {/* <input
              type="text"
              className="form-control"
              placeholder="Twitter Profile Link"
              {...register("twitter_profile")}
              name="twitter_profile"
              // value={fields.twitter_profile}
              // onChange={handleInputChange}
            /> */}
          </div>
        </div>
        <div className="row g-3">
          <div className="col-lg-6 mb-3">
            <TextField
              inputDivStyle={"form-control"}
              type="text"
              name="instagram_profile"
              register={register}
              // value={fields.instagram_profile}
              handleChange={handleInputChange}
              placeholder="Instagram Profile Link"
              errorMessage={errors?.instagram_profile?.message}
            />
            {/* <input
              type="text"
              className="form-control"
              placeholder="Instagram Profile Link"
              {...register("instagram_profile")}
              name="instagram_profile"
              // value={fields.instagram_profile}
              // onChange={handleInputChange}
            /> */}
          </div>
          <div className="col-lg-6 mb-3">
            <TextField
              inputDivStyle={"form-control"}
              type="text"
              name="youtube_profile"
              register={register}
              // value={fields.youtube_profile}
              handleChange={handleInputChange}
              placeholder="Youtube Profile Link"
              errorMessage={errors?.youtube_profile?.message}
            />
            {/* <input
              type="text"
              className="form-control"
              placeholder="Youtube Profile Link"
              {...register("youtube_profile")}
              name="youtube_profile"
              // value={fields.youtube_profile}
              // onChange={handleInputChange}
            /> */}
          </div>
        </div>
      </div>
      <div className="button-group">
        <button type="button" className="prev-slide" onClick={handleBackSocial}>
          <svg
            width="19"
            height="15"
            viewBox="0 0 19 15"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              d="M4.02714 6.61609L9.39338 1.24985L8.14352 0L0.643524 7.5L1.26845 8.12493L8.14352 15L9.39338 13.7501L4.02714 8.38391H18.3218V6.61609H4.02714Z"
              fill="black"
            />
          </svg>
          Previous Step
        </button>
        <button type="submit" className="btn btn-primary-fill next-slide">
          Submit
        </button>
      </div>
    </form>
  );
}

export default SocialNetworks;

const schema = yup.object({
  facebook_profile: yup
    .string()
    .matches(
      /((https?):\/\/)?(www.)?[a-z0-9]+(\.[a-z]{2,}){1,3}(#?\/?[a-zA-Z0-9#]+)*\/?(\?[a-zA-Z0-9-_]+=[a-zA-Z0-9-%]+&?)?$/,
      "Enter correct url!"
    ),
  instagram_profile: yup
    .string()
    .matches(
      /((https?):\/\/)?(www.)?[a-z0-9]+(\.[a-z]{2,}){1,3}(#?\/?[a-zA-Z0-9#]+)*\/?(\?[a-zA-Z0-9-_]+=[a-zA-Z0-9-%]+&?)?$/,
      "Enter correct url!"
    ),
  twitter_profile: yup
    .string()
    .matches(
      /((https?):\/\/)?(www.)?[a-z0-9]+(\.[a-z]{2,}){1,3}(#?\/?[a-zA-Z0-9#]+)*\/?(\?[a-zA-Z0-9-_]+=[a-zA-Z0-9-%]+&?)?$/,
      "Enter correct url!"
    ),
  youtube_profile: yup
    .string()
    .matches(
      /((https?):\/\/)?(www.)?[a-z0-9]+(\.[a-z]{2,}){1,3}(#?\/?[a-zA-Z0-9#]+)*\/?(\?[a-zA-Z0-9-_]+=[a-zA-Z0-9-%]+&?)?$/,
      "Enter correct url!"
    ),
});
