import React, { useEffect, useRef, useState } from "react";
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import * as yup from "yup";
import TextField from "../../../components/text-field";
import { useDispatch } from "react-redux";
import { personalInfoUpdate } from "../../../redux/actions/actions";
import Loading from "../../../components/loading";
import DatePicker from "react-date-picker";
import moment from "moment";

function PersonalInfo({ handlePersonalInfoBack, handleForward }) {
  const dispatch = useDispatch();
  const [fields, setFields] = useState({
    first_name: "",
    last_name: "",
    address: "",
  });
  const maxdate = moment().subtract(18, "years");

  const [maxdateoFBirth, setMaxdate] = useState(maxdate._d);
  const [value, onChange] = useState(maxdateoFBirth);

  const handleCloseCalender = () => {
    const maxdate = moment().subtract(18, "years");
    setMaxdate(maxdate._d);
  };

  const [errorMessage, setErrorMessage] = useState({
    dateOfBirth: "",
    file: "",
  });

  const [file, setFile] = useState();
  const [loading, setLoading] = useState();
  const [width, setWidth] = useState(16.6);
  // const [fileName, setFileName] = useState("");
  const [isFileUpload, setIsFileUpload] = useState(false);
  const [address, setAddress] = useState("");
  const user = JSON.parse(localStorage.getItem("user"));

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setAddress(value);
    // console.log(value,'dfjdfdsfkds')
    setFields({ ...fields, [name]: value });
  };
  const handleFocusedChanged = () => {
    const inputwidth = 2;
    if (fields.address) {
      setWidth(width + inputwidth);
    }
  };

  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm({
    mode: "onSubmit",
    resolver: yupResolver(schema),
  });

  const handleChange = (e) => {
    setFile(e.target.files[0]);
    // setFileName(e.target.files[0].name);
  };
  const userId = localStorage.getItem("userId");

  const formSubmit = (data) => {
    try {
      let formData = new FormData();
      formData.append("first_name", data.first_name);
      formData.append("last_name", data.last_name);
      formData.append("phone", data.phone);
      formData.append("nationality", data.nationality);
      formData.append("realationship_status", data.realationship_status);
      formData.append("spoken_languages", data.spoken_languages);
      formData.append("gender", data.gender);
      formData.append("date_of_birth", value.toLocaleDateString());
      if (!!data.about_me) {
        formData.append("about_me", data.about_me);
      } else {
        formData.append("about_me", null);
      }
      formData.append("address", data.address);
      formData.append("type", data.type);
      formData.append("user_id", userId);
      formData.append("file", data?.filecheck[0]);
      dispatch(personalInfoUpdate(formData, setLoading, handleForward));
    } catch (error) {
      console.log(error);
    }
  };

  const styles = {
    role: {
      backgroundColor: user?.roleId === "mentor" ? "#1790FF" : "#fdd417",
      width: `33.6%`,
      color: user?.roleId === "mentor" ? "#fff" : "#000",
    },
  };

  return (
    <div>
      <h3>Personalized Information</h3>
      <p className="p-default">
        Lorem Ipsum is simply dummy text of the printing and typesetting
        industry. Lorem Ipsum has been the industry's standard dummy text.
      </p>
      <div className="progress">
        <div
          style={styles.role}
          className="progress-bar-profile"
          role="progressbar"
          aria-label="Example with label"
          aria-valuenow="33.2"
          aria-valuemin="0"
          aria-valuemax="100"
        >
          33.6%
        </div>
      </div>

      <form onSubmit={handleSubmit(formSubmit)}>
        <div className="block-get-touch step-form mt-4">
          <div className="row g-3">
            <div className="col-lg-6 mb-3">
              <TextField
                inputDivStyle={"form-control"}
                type="text"
                name="first_name"
                register={register}
                // value={user.first_name}
                handleChange={handleInputChange}
                placeholder="First Name"
                defaultValue={`${user.first_name}`}
                errorMessage={errors?.first_name?.message}
              />
            </div>
            <div className="col-lg-6 mb-3">
              <TextField
                inputDivStyle={"form-control"}
                placeholder="Last Name"
                handleChange={handleInputChange}
                type="text"
                name="last_name"
                register={register}
                defaultValue={`${user.last_name}`}
                errorMessage={errors?.last_name?.message}
              />
            </div>
          </div>
          <div className="row g-3">
            <div className="col-lg-12 mb-3">
              <TextField
                inputDivStyle={"form-control"}
                type="number"
                name="phone"
                register={register}
                placeholder="Phone No *"
                errorMessage={errors?.phone?.message}
              />
            </div>
          </div>
          <div className="row g-3">
            <div className="col-lg-6 mb-3">
              <div className="select-form">
                <select
                  name=""
                  className="form-control"
                  id=""
                  {...register("nationality")}
                >
                  <option value="" selected hidden>
                    Nationality
                  </option>
                  <option value="Indian">Indian</option>
                  <option value="German">German</option>
                  <option value="Liberian">Liberian</option>
                  <option value="Mexican">Mexican</option>
                </select>
                {errors?.nationality?.message && (
                  <span className={"error_message"}>
                    {errors?.nationality?.message}
                  </span>
                )}
              </div>
            </div>
            <div className="col-lg-6 mb-3">
              <div className="select-form">
                <select
                  name=""
                  className="form-control"
                  id=""
                  {...register("realationship_status")}
                >
                  <option value="" selected hidden>
                    Relationship Status
                  </option>
                  <option value="Single">Single</option>
                  <option value="Married">Married</option>
                </select>
                {errors?.realationship_status?.message && (
                  <span className={"error_message"}>
                    {errors?.realationship_status?.message}
                  </span>
                )}
              </div>
            </div>
          </div>
          <div className="row g-3">
            <div className="col-lg-12 mb-3">
              <div className="select-form">
                <select
                  name=""
                  className="form-control"
                  id=""
                  {...register("spoken_languages")}
                  // onChange={handleSelectChangelanguages}
                >
                  <option value="" selected hidden>
                    Languages Spoken
                  </option>
                  <option value="English">English</option>
                  <option value="German">German</option>
                  <option value="Hindi">Hindi</option>
                </select>
                {errors?.spoken_languages?.message && (
                  <span className={"error_message"}>
                    {errors?.spoken_languages?.message}
                  </span>
                )}
              </div>
            </div>
          </div>
          <div className="row g-3">
            <div className="col-lg-12 mb-3">
              <textarea
                name="aboutUser"
                className="form-control"
                placeholder="Tell Us About Yourself"
                rows="3"
                {...register("about_me")}
                // onChange={handleInputChange}
                // value={fields.aboutUser}
              />
            </div>
          </div>
          <div className="row g-3">
            <div className="col-lg-6 mb-3">
              <div className="select-form">
                <select
                  name=""
                  className="form-control"
                  id=""
                  {...register("gender")}

                  // onChange={handleSelectGender}
                >
                  <option value="" selected hidden>
                    Gender
                  </option>
                  <option value="Male">Male</option>
                  <option value="Female">Female</option>
                  <option value="Other">Other</option>
                </select>
                {errors?.gender?.message && (
                  <span className={"error_message"}>
                    {errors?.gender?.message}
                  </span>
                )}
              </div>
            </div>
            <div className="col-lg-6 mb-3">
              <div className="datepicker-wrapper">
                {/* <input
                  type="text"
                  className="datepicker_input form-control"
                  placeholder="Date of Birth"
                  name="date_of_birth"
                  // value={fields.dateOfBirth}
                  // onChange={handleInputChange}
                  aria-label="Date of Birth"
                  {...register("date_of_birth")}
                />
                */}
                {/* <DatePicker
                  name="start_date"
                  className="form-control"
                  onChange={onChange}
                  value={value}
                  // minDate={value}
                  maxDate={value}
                  onCalendarClose={handleCloseCalender}
                /> */}

                <DatePicker
                  name="start_date"
                  className="form-control"
                  onChange={onChange}
                  value={value}
                />
              </div>
            </div>
          </div>
          <div className="row g-3">
            <div className="col-lg-12 mb-3">
              {/* <TextField
                inputDivStyle={"form-control"}
                type="text"
                name="address"
                value={fields.address}
                handleChange={handleInputChange}
                placeholder="Enter Address *"
                register={register}
                errorMessage={errors?.address?.message}
              /> */}
              <input
                className="form-control"
                placeholder="Enter Address *"
                name="address"
                {...register("address")}
                value={fields.address}
                onFocus={handleFocusedChanged}
                onChange={handleInputChange}
              />
              {errors?.address?.message && !address.length && (
                <span className={"error_message"}>
                  {errors?.address?.message}
                </span>
              )}
            </div>
          </div>
          <h4 className="my-3">Upload your ID Proof</h4>
          <div className="row g-3">
            <div className="col-lg-6">
              <div className="select-form">
                <select
                  name=""
                  className="form-control"
                  id=""
                  {...register("type")}
                >
                  <option value="" selected hidden>
                    Select ID Proof
                  </option>
                  <option value="Driving Licence">Driving Licence</option>
                  <option value="Passport">Passport</option>
                </select>
                {errors?.type?.message && (
                  <span className={"error_message"}>
                    {errors?.type?.message}
                  </span>
                )}
              </div>
            </div>
            <div className="col-lg-6">
              <input
                // ref={register}
                type="file"
                className="form-control"
                name="filecheck"
                onChange={handleChange}
                {...register("filecheck")}
              />
              {errors?.filecheck?.message && (
                <span className={"error_message"}>
                  {errors?.filecheck?.message}
                </span>
              )}
            </div>
          </div>
        </div>
        <div className="button-group">
          <button
            type="button"
            className="prev-slide"
            onClick={handlePersonalInfoBack}
          >
            <svg
              width="19"
              height="15"
              viewBox="0 0 19 15"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M4.02714 6.61609L9.39338 1.24985L8.14352 0L0.643524 7.5L1.26845 8.12493L8.14352 15L9.39338 13.7501L4.02714 8.38391H18.3218V6.61609H4.02714Z"
                fill="black"
              />
            </svg>
            Previous Step
          </button>
          <button type="submit" className="btn btn-primary-fill next-slide">
            {loading === true ? (
              <Loading loaderClass="loading_button" />
            ) : (
              "Next Step"
            )}
            <svg
              width="18"
              height="15"
              viewBox="0 0 18 15"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M14.6164 6.61609L9.25018 1.24985L10.5 0L18 7.5L17.3751 8.12493L10.5 15L9.25018 13.7501L14.6164 8.38391H0.321777V6.61609H14.6164Z"
                fill="white"
              />
            </svg>
          </button>
        </div>
      </form>
    </div>
  );
}

export default PersonalInfo;

const phoneRegExp =
  /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/;

const schema = yup.object({
  first_name: yup
    .string()
    .required("First Name is Required")
    .matches(/^[A-Za-z ]*$/, "Please enter valid name"),
  last_name: yup
    .string()
    .required("Last Name is Required")
    .matches(/^[A-Za-z ]*$/, "Please enter valid name"),
  filecheck: yup
    .mixed()
    .test("name", "Please Provide a File", (value) => {
      return value[0] && value[0].name !== "";
    })
    .test("fileSize", "The file is too large", (value) => {
      return value[0] && value[0].size <= 1000000000;
    }),
  // .test("type", "We only support image", (value) => {
  //   return value[0] && value[0].type.includes("image");
  // }),
  phone: yup
    .string()
    .required("Phone Number is Required")
    .matches(phoneRegExp, "Please enter valid mobile number"),
  nationality: yup.string().required("Please select one option"),
  realationship_status: yup.string().required("Please select one option"),
  spoken_languages: yup.string().required("Please select one option"),
  about_me: yup.string(),
  gender: yup.string().required("Please select one Option"),
  type: yup.string().required("ID Proof is required"),
  address: yup.string().required("Address is required"),
});
