import {
  EMAIL_VERFIED,
  LOGIN,
  SIGNUP,
  UPDATECUSTODIALINFO,
  UPDATEDUCATIONALINFO,
  UPDATEPERSONALINFO,
} from "../constants/constants";

const initialState = {
  token: {},
  signup: {},
  userData: {},
  user: JSON.parse(localStorage.getItem("user")),
  verfied: "",
  personalInfo: {},
  educationalInfo: {},
  custodialInfo: {},
};

const mainreducer = (state = initialState, action) => {
  const { type, payload } = action;

  switch (type) {
    case SIGNUP:
      return {
        ...state,
        signup: { ...payload },
      };
    case LOGIN:
      return {
        ...state,
        userData: { ...payload },
      };
    case UPDATEPERSONALINFO:
      return {
        ...state,
        personalInfo: { ...payload },
      };
    case UPDATEDUCATIONALINFO:
      return {
        ...state,
        educationalInfo: { ...payload },
      };
    case UPDATECUSTODIALINFO:
      return {
        ...state,
        custodialInfo: { ...payload },
      };
    case EMAIL_VERFIED:
      return {
        ...state,
        verfied: true,
      };
    default:
      return {
        ...state,
      };
  }
};

export default mainreducer;
