import React, { useState, useEffect, useCallback } from "react";
import { Link, useLocation } from "react-router-dom";
import "./profile.css";
import { createNotification } from "../../common/create-notifictions";
import validations from "./validations";
import TextField from "../../components/text-field";
import { Datepicker } from "vanillajs-datepicker";
import Cropper from "react-easy-crop";
import Loading from "../../components/loading";
import getCroppedImg from "../../components/getCroppedImage";
import Upload from "./steps/upload";
import Navbar from "../../components/navbar/index";
import PersonalInfo from "./steps/personalInfo";
import Educationalinfo from "./steps/Educationalinfo";
import CustoDial from "./steps/Custodial";
import WorkDetails from "./steps/workdetails";
import SocialNetworks from "./steps/SocialNetworks";

function Profile() {

  const { pathname } = useLocation();
  useEffect(() => {
    window.scrollTo(0, 0);
  }, [pathname]);

  const [steps, setSteps] = useState(1);

  const [fields, setFields] = useState({
    image: "",
    firstName: "",
    lastName: "",
    phoneNumber: "",
    nationality: "",
    relationShipStatus: "",
    languages: "",
    aboutUser: "",
    gender: "",
    dateOfBirth: "",
    address: "",
    selectIdProof: "",
    idProofFile: "",
    school: "",
    dateAttendedto: "",
    dateAttendedfrom: "",
    degree: "",
    specialization: "",
    description: "",
    startPrisionDate: "",
    endPrisionDate: "",
    prisionTimeDuration: "",
    issueConfrontingtogetJob: "",
    capabilities: "",
    detailsExplanation: "",
    company: "",
    companyFrom: "",
    companyTo: "",
    position: "",
    fbLink: "",
    location: "",
    companyDescription: "",
    twitterLink: "",
    instagramLink: "",
    youtubeLink: "",
    description: "",
  });

  const [worddetails, setWorkDetails] = useState([
    {
      company: "",
      from: "",
      to: "",
      position: "",
      location: "",
      description: "",
    },
  ]);

  const [errors, setErrors] = useState({});

  useEffect(() => {
    const getDatePickerTitle = (elem) => {
      const label = elem.nextElementSibling;
      let titleText = "";
      if (label && label.tagName === "LABEL") {
        titleText = label.textContent;
      } else {
        titleText = elem.getAttribute("aria-label") || "";
      }
      return titleText;
    };

    const elems = document.querySelectorAll(".datepicker_input");
    for (const elem of elems) {
      const datepicker = new Datepicker(elem, {
        format: "dd/mm/yyyy", // UK format
        title: getDatePickerTitle(elem),
        autohide: true,
      });
    }
  }, []);

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFields({
      ...fields,
      [name]: value,
    });

    setErrors(validations(fields));
  };

  return (
    <>
      <Navbar />

      <div className="scroll-wrapper">
        <div className="container-fluid p-0">
          <div className="step-wrapper">
            {/* Left  */}
            <div
              className="quote-left text-white"
              style={{ background: `url(images/quote-bg.png)` }}
            >
              <div className="quote-wrapper">
                <h3 className="quote">
                  Let's take the first step towards your{" "}
                  <span>bounceback.</span>
                </h3>
                <p className="small-text">
                  It's our pleasure to guide you every step of the way.
                </p>
                <div className="small-text copyright">
                  &copy;2022 Bounceback. All right reserved.
                </div>
              </div>
            </div>

            {/* Ends  */}

            <div className="form-right">
              <div
                // onSubmit={handleSubmit}
                className="step-form"
                autoComplete="false"
              >
                {/* Upload Profile Picture  */}

                <div
                  className={steps === 1 ? "step upload-photo d-block" : "step"}
                >
                  <Upload stepStep={() => setSteps(2)} />
                </div>

                {/* Upload Profile Picture ends */}

                {/*PersonalInformation */}

                <div
                  className={
                    steps === 2
                      ? "step personalized-information d-block"
                      : "step"
                  }
                >
                  <PersonalInfo
                    handlePersonalInfoBack={() => setSteps(1)}
                    handleForward={() => setSteps(3)}
                  />
                </div>

                {/*PersonalInformation Ends*/}

                {/*Educational Informations */}

                <div
                  className={
                    steps === 3
                      ? "step educational-information d-block"
                      : "step "
                  }
                >
                  <Educationalinfo
                    handleBackEducInfo={() => setSteps(2)}
                    handleForwardEducInfo={() => setSteps(4)}
                  />
                </div>

                {/*Educational Informations ends */}

                {/*Custodial  */}

                <div
                  className={
                    steps === 4 ? "step custodial-detail d-block" : "step "
                  }
                >
                  <CustoDial
                    handleForwardcustodial={() => setSteps(5)}
                    handlecustoDialBack={() => setSteps(3)}
                  />
                </div>
                {/*Custodial  ends*/}

                {/* Word Details  */}

                <div
                  className={
                    steps === 5 ? "step work-details d-block" : "step "
                  }
                >
                  <WorkDetails
                    handleForwardworkDetails={() => setSteps(6)}
                    handleworkDetailsBack={() => setSteps(4)}
                  />
                </div>
                {/* Ends  */}

                {/* Social networks  */}
                <div
                  className={
                    steps === 6 ? "step social-details d-block" : "step "
                  }
                >
                  <SocialNetworks handleBackSocial={() => setSteps(5)} />
                </div>
                {/* Ends  */}
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Profile;
